* {
  --bluecolor: #00acdc;
  margin: 0;
}

body {
  text-transform: uppercase;
  background-color: #444;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.nav {
  margin-right: 50px;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav__list {
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  margin-left: 20px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active {
  color: var(--bluecolor);
  margin-left: 0;
}

.nav__link:hover {
  color: var(--bluecolor);
}

.is-active:after {
  content: "";
  background-color: var(--bluecolor);
  border-radius: 5px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
